<template>
  <div class="index">
    <h2 class="title">吉林省农民工欠薪维权电话导图</h2>
    <div class="mapMain">
      <div class="mychart" id="mychart"></div>
    </div>
    <div class="more">
      <div class="topTitle">{{selectCity.name}}</div>
      <div class="mainMap">
        <ul class="list" v-if="selectCity">
          <li class="listli" v-for="(item,index) in selectCity.phoneList" :key="index">
            <div class="titles"><div class="titleTag">{{item.name}}</div></div>
            <div class="tel">
              <div class="telMain">
                <div class="telName">举报电话:</div>
                <ul>
                  <li v-for="(itemj,indexj) in item.phone" :key="indexj">
                    <a :href="'tel:'+itemj">{{itemj}}</a>
                  </li>
                </ul>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ComplaintMap",
  data() {
    return {
      cityList: [],
      selectCity: {},
    };
  },
  components: {
  },
  methods: {
    mapInit() {
      // 初始化
      let mychart = this.echarts.init(document.getElementById("mychart"));
      // 监听屏幕变化自动缩放图表
      window.addEventListener("resize", function () {
        mychart.resize();
      });
      mychart.setOption({
        trigger: "item",
        series: [
          {
            type: "map",
            map: "吉林省",
            // 地图类型
            mapType: "province",
            selectedMode: "single",
            zoom: 1.2,
            roam: false,
            data: this.cityList,
            nameMap: {},
            label: {
              show: true,
              color: "#FFF",
              fontSize: "12",
            },
            itemStyle: {
              borderWidth: 0.5, //边框大小
              borderColor: "#ccc",
              areaColor: "#0070bc", //背景颜色
            },
            label: {
              show: true, //显示名称
              color: "#FFF",
            },
            emphasis: {
              //高亮
              itemStyle: {
                borderWidth: 1,
                borderColor: "#ccc",
                areaColor: "red",
                color: "#FFF",
              },
              label: {
                show: true,
                color: "#fff",
              },
            },
            select: {
              //选中
              itemStyle: {
                areaColor: "red",
              },
              label: {
                color: "#FFF",
              },
            },
          },
        ],
      });
      mychart.on("click", (params) => {
        this.selectCity = params.data
      });
    },
    init() {
      this.$http.jsb.getMapData().then((res) => {
        this.cityList = res
        this.cityList.forEach(item => {
          if(item.name == '长春市'){
            item.selected = true;
            this.selectCity = item
          }
        });
        this.mapInit();
      });
    },
  },
  created () {
    //获得吉林省下城市数据
    this.init();
  },
};
</script>

